export default function sketch(p) {
  let canvas;
  var inc = 0.01;
  var start = 0;
  p.setup = () => {
    canvas = p.createCanvas(1300, 160);
  };

  p.draw = () => {
    p.background("#1f262a");
    p.stroke(255);
    p.noFill();
    p.beginShape();
    var xoff = start;
    for (var x = 0; x < p.width; x++) {
      p.stroke(255);
      var y = p.noise(xoff) * p.height;
      p.vertex(x, y);
      xoff += inc;
    }
    p.endShape();
    start += inc;
  };
}
