import React, { Component } from "react";

class ProjectWindow extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: props.title,
      subtitle: props.subtitle,
      description: props.description,
      url: props.url,
      pic: props.pic
    };
  }
  render() {
    return (
      <div className="col-sm-12 col-md-4">
        <img src={this.state.pic} />
        <div className="lightbox container-fluid">
          <div id="projectText" className="projectHead">
            {this.state.title}
          </div>
          <div id="projectText" className="projectSub">
            {this.state.subtitle}
          </div>
          <div id="projectText" className="projectBody">
            {this.state.description}
          </div>
        </div>
      </div>
    );
  }
}

export default ProjectWindow;
